.card {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 200px;
  height: 300px;
  padding: 15px;
  margin: 0 auto;
  border: 1px solid #000;
  border-radius: 10px;
  box-shadow: 5px rgb(110, 110, 110);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 12px;
  &__image {
    width: 60%;
    text-align: center;
    &_wrap {
      text-align: center;
      height: 180px;
    }
  }
  &__name {
    text-align: center;
    font-weight: bold;
    margin-bottom: 0;
  }
  &__description {
    text-align: center;
  }
  &__button {
    margin: 0 auto;
    cursor: pointer;
  }
}